import { useState, useEffect } from 'react';

export interface Zone {
    id: number;
    location: string;
    lat: number;
    lng: number;
    severity: 'HIGH' | 'MODERATE' | 'LOW';
    type: string;
    description: string;
    active_hours?: string;
    radius?: number; // Optional, defaults will be applied
}

export const useZones = () => {
    const [zones, setZones] = useState<Zone[]>([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);

    const fetchZones = async () => {
        try {
            const response = await fetch('http://localhost:8000/zones');
            if (!response.ok) {
                throw new Error('Failed to fetch zones');
            }
            const data = await response.json();
            // Expecting data format: { zones: [...] }
            setZones(data.zones || []);
            setError(null);
        } catch (err) {
            console.error('Error fetching zones:', err);
            // FALLBACK: Use mock data when backend is unavailable
            setZones([
                {
                    id: 1,
                    location: "TASMAC Katpadi",
                    lat: 12.9716,
                    lng: 79.1594,
                    severity: 'HIGH',
                    type: 'crime_hotspot',
                    description: 'High crime area near TASMAC',
                    radius: 200
                },
                {
                    id: 2,
                    location: "VIT Main Gate",
                    lat: 12.9692,
                    lng: 79.1559,
                    severity: 'MODERATE',
                    type: 'moderate_risk',
                    description: 'Moderate risk zone',
                    radius: 150
                }
            ]);
            setError(null); // Don't show error if we have fallback data
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        fetchZones();
        // Poll every 60 seconds for zone updates (e.g. time-based activation)
        const interval = setInterval(fetchZones, 60000);
        return () => clearInterval(interval);
    }, []);

    return { zones, loading, error };
};
